
@font-face {
  font-family: "PixelSans";
  src: url('fonts/PixeloidSans.ttf');
 
}

.logo {
    width: 200px;
    height: 100px;
}

#running-horse img {
  display: none;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
}

#background {
    background-image: url("../assets/images/longfarm.png");
    background-size: 425%;
}

.hide {
    display: none;
}

#start-button {
    cursor: pointer;
}

#restart-button {
  cursor: pointer;
}

.start-modal {
    display: none;
    position: fixed;
    z-index: 1;
    margin-top: 175px;
    width: 40%;
    height: 42%;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .start-modal-content {
    background-color: transparent;
    text-align: center;
    font-family: "PixelSans";
    font-size: 30px;
    color: white;
  }

  .end-modal {
    display: none;
    position: fixed;
    z-index: 1;
    margin-top: 175px;
    width: 30%;
    height: 32%;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .end-modal-content {
    background-color: transparent;
    text-align: center;
    padding: 30px;
    font-family: "PixelSans";
    font-size: 30px;
    color: white;
  }

  #links {
    display: flex;
    align-items: center;
  }
